@media screen and (max-width: 1439px) {

  .burger-menu {
    display: block;
    position: absolute;
    // top: 20px;
    right: 20px;
    background: none;
    border: none;
    cursor: pointer;
    // z-index: 1000;

    svg.icon {
      width: 24px;
      height: 24px;
      fill: #5932ea;
    }
  }

  .main-content {
    padding: 30px 15px 15px 15px;
    background-color: #fafbff;
    &__header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 20px; // Добавьте отступ снизу, если нужно
    }
  
    &__title {
      margin-top: 0;
      margin-bottom: 0;
      margin-left: 10px;
      font-size: 22px;
      font-weight: 600;
      line-height: normal;
      letter-spacing: -0.22px;
    }
  }

  .customers__table-container {
    overflow-x: auto;

    .customers__table {
      width: 865px;
    }
  }
  //customers
  .customers {
    background-color: #fff;
    filter: drop-shadow(0px 10px 60px rgba(226, 236, 249, 0.5));
    border-radius: 10px;
    padding: 10px 0px 10px 10px;

    &__title {
      margin-top: 0;
      margin-bottom: 30px;
      font-size: 18px;
      font-weight: 600;
      line-height: normal;
      letter-spacing: -0.22px;
    }

    &__subtitle {
      color: #16c098;
      font-family: 'Poppins', sans-serif;
      font-weight: 400;
      letter-spacing: -0.14px;
      margin-bottom: 0;
      margin-top: 0;
    }
    &__header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 20px;
    }

    &__search {
      position: relative;
      display: flex;
      align-items: center;
 
  
      svg {
        position: absolute;
        left: 8px;
        top: 50%;
        transform: translateY(-50%);
        width: 24px;
        height: 24px;
        pointer-events: none; 
      }
  
      input {
        border-radius: 10px;
        background: #f9fbff;
        width: 136px;
        height: 38px;
        padding-left: 40px;
        border: none;
        color: #b5b7c0;
        font-size: 12px;
        font-weight: 400;
        letter-spacing: -0.12px;
      }
    
    &__table {
      width: 100%;
      border-collapse: separate; 
      border-spacing: 0 10px;

  }
  }

  &__table {
    width: 100%;
    border-collapse: separate; 
    border-spacing: 0 10px;

    thead {
      position: relative;
      tr{
      th.customers__th{
        text-align: left;
      }
    }

      &::after {
        content: '';
        position: absolute;
        bottom: -5px; // Чтобы линия не перекрывалась ячейками
        left: -40px;
        width: 950px;
        height: 1px;
        background: #eee;
      }
    }

    tbody {
      tr {
        position: relative;

        &::after {
          content: '';
          position: absolute;
          bottom: -5px; 
          left: 0;
          width: 840px;
          height: 1px;
          background: #eee;
        }
      }
    }
  }

  &__th {
    text-align: left;
    color: #b5b7c0;
    font-weight: normal;
    // padding: 10px;
    font-weight: 500;
    line-height: normal;
    letter-spacing: -0.14px;
  }
  &__td {
    // padding: 10px 0px;
    color: #292d32;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    letter-spacing: -0.14px;

    &--active {
      margin-top: 5px;

      color: #008767;
      background: #16c09861;
      border: 1px solid #00b087;
      border-radius: 4px;
      justify-content: center;
      align-items: center;
      padding: 4px 12px;
      font-weight: 500;
      display: inline-flex;
      width: 84px;
    }

    &--inactive {
      margin-top: 5px;

      color: #df0404;
      letter-spacing: -0.14px;
      background: #ffc5c5;
      border: 1px solid #df0404;
      border-radius: 4px;
      justify-content: center;
      align-items: center;
      padding: 4px 12px;
      font-weight: 500;
      display: inline-flex;
      width: 84px;
    }
  }

  &__pagination {

    margin-top: 30px; 
  }

  &__pagination-info {
    color: #b5b7c0;
    line-height: normal;
    letter-spacing: -0.14px;
  }

  &__pagination-buttons {
    display: flex;
    gap: 12px;
    margin-right: 30px;
  }

  &__pagination-button {
    border-radius: 4px;
    border: 1px solid #eee;
    padding: 6px 9px;
    background: transparent;
    cursor: pointer;
    color: #404b52;
    font-size: 12px;
    font-weight: 500;
    line-height: 1;
    letter-spacing: -0.12px;

    &--active {
      border: 1px solid #5932ea;
      background: #5932ea;
      color: white;
    }
  }




}



}